<template>
  <div>

    <mdb-container>
      <h1>Plaaditav sein</h1>
      <mdb-row>
        <mdb-col id="panel" lg="3">
          <mdb-input label="Enter wall width" size="lg" v-model="wallx" />
          <mdb-input label="Enter wall height" size="lg" v-model="wally" />
          <mdb-btn block color="green" @click="createRect">Loo nelinurk</mdb-btn>
        </mdb-col>
        <mdb-col id="container">
          <canvas id="wall" width="400" height="300"></canvas><br>
        </mdb-col>

      </mdb-row>

    </mdb-container>
  </div>
</template>

<script>
import {mdbContainer, mdbBtn, mdbRow, mdbCol, mdbInput} from 'mdbvue'
export default {
name: "main-page",
  components: {
    mdbContainer,
    mdbBtn,
    mdbRow,
    mdbCol,
    mdbInput
  },
  data () {
  return {
    wall: null,
    wallx: null,
    wally: null,
    x_full_tiles: 0
    }
  },
  mounted() {
    const c = document.getElementById('wall')
    this.wall = c.getContext('2d')
  },
  methods: {
    createRect() {
      let cx = this.wall
      //cx.createRect(100, 100, 50, 50)
      //cx.beginPath();

      //cx.rect(20, 20, 100, 100);
      //cx.stroke();

      this.x_full_tiles = 400 / (20 + 3)
      console.log("Wall full tiles ", this.x_full_tiles)
      this.x_full_tiles = (this.x_full_tiles | 0) - 1
      console.log("Wall full tiles ", this.x_full_tiles)
      for(let x = 0; x < this.x_full_tiles; x++) {
        const xPos = 10 + x * (20 + 3)

        for(let y = 0; y < 5; y++) {
          const yPos = 10 + y * (20 + 3)

          cx.fillStyle = "red"

          cx.fillRect(xPos, yPos, 20, 20)

        }
      }
    },

  }
}
</script>

<style scoped>
  #wall {
    border: 1px solid gray;
  }

  #panel {
    border: 1px solid red;
    padding: 20px;
  }

  #container {
    border: 1px solid green;
    padding-top: 20px;
    text-align: center;
  }
</style>